/* FULLY RESPONSIVE CSS GRID */
.about-grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 20px;
  -ms-flex-align: stretch;
      align-items: stretch;
  color: white;
  padding: 10px;
  background-color: none;
  text-align: center;
  padding: 20px 0;
  font-size: 15px;
}
.about-content-wrapper {
    padding: 30px;
}

.about-image {
  width: 100%;
  height: 100%;
  overflow: hidden;
}



