
.navbar {
    position:fixed;
    z-index: 1000;
    left:0px;
    top:0px;
    height:57px;
    width:100%;
    background:rgb(0, 0, 0, .4);
    text-align: center;
    margin: 0px;

 }

.navbar-line {
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-direction: row; 
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-line-pack: stretch;
        align-content: stretch;
    border-bottom: 1px solid white;
    height: 100%;
}

 .navbar-line div {
    margin: 0 5px 0 5px;
 }

 .navbar a {
     text-decoration: none;
     color: white;
 }

 .navbar a:hover {
    color: chartreuse;
}

.navbar img{
    padding: 4px 0 4px 0;
    margin: 0 5px 0 5px;
    background-color: rgb(0, 0, 0, 0.75);
}



